* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #0c0c1e 0%, #1a1a2e 50%, #16213e 100%);
  min-height: 100vh;
  position: relative;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    radial-gradient(circle at 20% 50%, rgba(64, 224, 208, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 20%, rgba(64, 224, 208, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 40% 80%, rgba(64, 224, 208, 0.08) 0%, transparent 50%);
  pointer-events: none;
  z-index: -1;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.03;
  background: 
    /* Simple chain patterns scattered across the background */
    radial-gradient(circle at 100px 200px, rgba(64, 224, 208, 0.4) 2px, transparent 2px),
    radial-gradient(circle at 130px 200px, rgba(64, 224, 208, 0.4) 2px, transparent 2px),
    radial-gradient(circle at 160px 200px, rgba(64, 224, 208, 0.4) 2px, transparent 2px),
    radial-gradient(circle at 190px 200px, rgba(64, 224, 208, 0.4) 2px, transparent 2px),
    
    radial-gradient(circle at 400px 350px, rgba(64, 224, 208, 0.4) 2px, transparent 2px),
    radial-gradient(circle at 430px 350px, rgba(64, 224, 208, 0.4) 2px, transparent 2px),
    radial-gradient(circle at 460px 350px, rgba(64, 224, 208, 0.4) 2px, transparent 2px),
    
    radial-gradient(circle at 700px 150px, rgba(64, 224, 208, 0.4) 2px, transparent 2px),
    radial-gradient(circle at 730px 150px, rgba(64, 224, 208, 0.4) 2px, transparent 2px),
    radial-gradient(circle at 760px 150px, rgba(64, 224, 208, 0.4) 2px, transparent 2px),
    radial-gradient(circle at 790px 150px, rgba(64, 224, 208, 0.4) 2px, transparent 2px),
    
    /* Connecting bonds */
    linear-gradient(0deg, transparent 49%, rgba(64, 224, 208, 0.2) 50%, transparent 51%),
    linear-gradient(0deg, transparent 49%, rgba(64, 224, 208, 0.2) 50%, transparent 51%),
    linear-gradient(0deg, transparent 49%, rgba(64, 224, 208, 0.2) 50%, transparent 51%);
  background-size: 
    300px 250px, 300px 250px, 300px 250px, 300px 250px,
    300px 250px, 300px 250px, 300px 250px,
    300px 250px, 300px 250px, 300px 250px, 300px 250px,
    120px 1px, 90px 1px, 150px 1px;
  background-position: 
    0 0, 0 0, 0 0, 0 0,
    300px 250px, 300px 250px, 300px 250px,
    600px 50px, 600px 50px, 600px 50px, 600px 50px,
    115px 200px, 415px 350px, 715px 150px;
  background-repeat: no-repeat;
  pointer-events: none;
  z-index: -1;
  animation: chainDrift 35s infinite linear;
}

@keyframes chainDrift {
  0% { transform: translateX(0) translateY(0); }
  100% { transform: translateX(-30px) translateY(-20px); }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
